.todo-item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 30px 20px 0;
  width: 70%;
  border-bottom: 1px solid#cecece;
  font-weight: 100;
  font-style: 15px;
  color: #333333;
}

input[type=checkbox] {
  margin-right: 10px;
  font-size: 30px;
}

input[type=checkbox]:focus {
  outline: 0;
}

.completed{
  color: green;
  text-decoration: line-through;
  font-style: italic;
}

.notCompleted{
  color: red;
  font-style: italic;
}